<template>
  <div>
    <div class="bg">
      <div class="title">我的投递</div>
      <div class="list">
        <div>
          <Table :columns="columns1" :data="data">
            <template slot-scope="{ row, index }" slot="mmName">{{row.moneyManage.institutions}}</template>
            <template slot-scope="{ row, index }" slot="pmName">{{row.projectManage.name}}</template>
            <template slot-scope="{ row, index }" slot="caozuo">
              <Button
                @click="clickprojectDeliveryAdd(row.moneyManage.code,row.projectManage.code)"
                style="margin-right:10px;"
              >再次投递</Button>
              <Button
                :to="`/project/detail/${row.projectManage.id}`"
                style="margin-right:10px;"
              >查看项目</Button>
              <Button :to="`/capital/detail/${row.moneyManage.id}`">查看资金</Button>
            </template>
          </Table>
        </div>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getProjectDeliverys, projectDeliveryAdd } from "../../../api/public";
import { messages } from "../../../utils";
import { Icon, Button, Page, Tag, Table, Select, Option, Input } from "iview";
import { getCollect } from "../../../api/public";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Table.name]: Table,
    [Tag.name]: Tag,
    [Select.name]: Select,
    [Option.name]: Option,
    [Input.name]: Input,
    [Button.name]: Button
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        type: "1",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      data: [],
      columns1: [
        {
          title: "项目名称",
          slot: "pmName"
        },
        {
          title: "资金名称",
          slot: "mmName"
        },
        {
          title: "投递时间",
          key: "createTime",
          width: 200
        },
        {
          title: "操作",
          slot: "caozuo",
          width: 350
        }
      ]
    };
  },
  methods: {
    getData() {
      getProjectDeliverys(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    /**
     * @function 我要投递
     * @param {projectCode 项目code}
     */
    clickprojectDeliveryAdd(moneyCode, projectCode) {
      projectDeliveryAdd({ moneyCode, projectCode }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.getData();
          messages.success(`项目投递成功`);
        }
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    }
  },
  created() {
    this.getData();
  }
};
</script>
