  min-height: 600px;
  background: #ffffff;
  padding: 20px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    margin-bottom: 20px;
    color: #2868d1;
    font-size: 16px;
  }
  .list {
    .ivu-table-header {
      overflow: hidden;
      font-size: 14px;
      font-weight: 400;
    }
    .page {
      margin-top: 30px;
      padding-bottom: 60px;
      width: 100%;
      text-align: center;
    }
  }
}
